.header {
    background-color: $gray_color;
    padding: 10px;
    &_top {
        display: flex;
        justify-content: space-between;
        margin-left: 60px;
        margin-top: 30px;

        @media screen and(max-width: 900px) {
            flex-direction: column;
            position: fixed;
            z-index: 1;
            background-color: #000000;
            right: -100%;
            width: 80%;
            padding: 0;
        }
        &.active {
            right: 0;
        }
    }
    &_list {
        display: flex;
        justify-content: space-between;
        list-style: none;
        @media screen and(max-width: 900px) {
            flex-direction: column;
            width: auto;
        }
    }
    &_item {
        border: 1px solid $border_color;
        margin-right: 30px;
    }
    &_link {
        text-decoration: none;
        font-size: 18px;
        font-weight: 500;
        color: $button_text;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 150px;
        height: 45px;
        padding: 10px;
        @media screen and(max-width: 575px) {
            width: auto;
        }
        @media screen and(min-width: 425px) and (max-width: 900px) {
            margin: auto;
        }
        @media screen and (min-width: 900.99px) and (max-width: 1024px) {
            width: 120px;
        }
    }
    &_body {
        margin-top: 100px;
        display: flex;
        justify-content: space-between;
        @media screen and(max-width: 900px) {
            flex-direction: column;
        }
    }
    &_article {
        position: relative;
        width: 550px;
        height: 180px;
        background-color: $article_color;
        @media screen and(max-width: 900px) {
            width: auto;
        }
        &:before {
            content: "";
            position: absolute;
            bottom: 0;
            width: 0;
            height: 0;
            border-bottom: 210px solid $gray_color;
            border-right: 50px solid transparent;
            @media screen and(max-width: 1024px) {
                display: none;
            }
        }
        &:after {
            content: "";
            position: absolute;
            right: 0;
            bottom: 0;
            width: 0;
            height: 0;
            border-top: 210px solid $gray_color;
            border-left: 50px solid transparent;
            @media screen and(max-width: 1024px) {
                display: none;
            }
        }
    }
    &_bot {
        width: 100%;
        background-image: url("../img/general/bot.png");
        background-size: cover;
        background-repeat: no-repeat;
        height: 60vh;
        @media screen and (max-width: 320px) {
            height: 15vh;
        }
        @media screen and (min-width: 320.99px) and (max-width: 375px) {
            height: 17vh;
        }
        @media screen and (min-width: 375.99px) and (max-width: 425px) {
            height: 19vh;
        }
        @media screen and (min-width: 425.99px) and (max-width: 768px) {
            height: 36vh;
        }
        @media screen and (min-width: 768.99px) and (max-width: 1024px) {
            height: 36vh;
        }
    }
    &_paragraph {
        font-size: 18px;
        color: #6d6969;
        text-align: justify;
        padding: 0 45px;
        @media screen and (max-width: 320px) {
            font-size: 17px;
            padding: 0 15px;
        }
        @media screen and (min-width: 425.99px) and (max-width: 768px) {
            font-size: 25px;
            padding: 0 15px;
        }
    }
    &_hamburger {
        position: absolute;
        top: 0;
        right: 0;
        width: 40px;
        height: 40px;
        background-color: #c4c4c4;
        @media screen and (min-width: 900px) {
            display: none;
        }
        &_line {
            height: 8px;
            background-color: #000000;
            transition: all ease .3s;

            &:not(:last-child) {
                margin-bottom: 8px;
            }
        }
    }
}
.logo {
    width: 15%;
    padding: 20px 25px;
    background-color: $logo_color;
    border-radius: 50%;
    text-align: center;
    font-size: 12px;
    margin-right: 10%;
    @media screen and(max-width: 575px) {
        margin-bottom: 10px;
        width: 40%;
    }
}

